@import '../../global';

.textComponent-wrapper{
    text-align: center;
    display: flex;
    margin: 0 auto;
    padding: 1.5rem 0;
    flex-direction: column;
    justify-content: center;
    width: 60%;
    h4{
        color: darken($primary, 30);
        font-size: 28px;
        font-weight: bold;
    }
    p{
        color: $text-dark;
        line-height: 1.5rem;
        margin-top: 1rem;
    }
}

@media only screen and (max-width: 680px){
    .textComponent-wrapper{
        padding: 1.5rem 0;
        width: 100%;
        h4{
            font-size: 24px;
        }
    }
}