@import "../../global";

.cardKelas-wrapper {
  border-radius: 10px;
  cursor: pointer;
  border: 2px solid white;
  position: relative;
  margin: 1rem 0;
  &:hover {
    box-shadow: 8px 8px 1rem rgba(66, 66, 66, 0.562);
    transform: translateY(-8px);
  }
  img {
    object-fit: cover;
    width: 100%;
    max-height: 160px;
    border-radius: 10px;
  }
  .bg-card {
    background-color: #193820d0;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1rem;
    border-radius: 10px;
    color: whitesmoke;
    h5 {
      font-size: 24px;
      line-height: 32px;
    }
    .mentor {
      font-weight: 500;
      color: $text-white;
    }
    .kelas-tersedia-wrapper{
      padding: 12px;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: $primary;
      opacity: 0.7;
      border-radius: 0 0 10px 10px;
      .kelas-tersedia{
        justify-content: center;
        border-radius: $radius-button;
        color: white;
      }
    }
    .disable {
      background-color: $primary;
      z-index: 10;
      position: absolute;
      top: 0;
      right: 0;
      padding: 12px 1rem;
      border-radius: 10px;
      margin: 6px;
      // filter: blur(2px);
      p {
        color: whitesmoke;
      }
    }
  }
  .bg-disable {
    background-color: #0f1f13dc;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1rem;
    border-radius: 10px;
    color: whitesmoke;
    cursor: not-allowed;
    h5 {
      font-size: 24px;
      line-height: 32px;
      filter: blur(2px);
    }
    .mentor {
      font-weight: 500;
      color: $text-white;
      filter: blur(2px);
    }
    .kelas-tersedia-wrapper{
      padding: 12px;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: $primary;
      opacity: 0.7;
      border-radius: 0 0 10px 10px;
      .kelas-tersedia{
        justify-content: center;
        border-radius: $radius-button;
        color: white;
      }
    }
    .disable {
      background-color: $primary;
      z-index: 2;
      position: absolute;
      top: 0;
      right: 0;
      padding: 12px 1rem;
      border-radius: 10px;
      margin: 6px;
      p {
        color: whitesmoke;
        filter: blur(0);
      }
    }
    
  }
}
// responsive
@media only screen and (max-width: 768px) {
  .cardKelas-wrapper {
    margin: 0;
  }
  .cardKelas-wrapper img {
    position: relative;
    transform: none;
    margin-top: 0;
  }
}

