@import "../../global";

nav {
  background-color: $primary;
  height: 70px;
  // padding: 0.8rem 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  align-items: center;
  .navbar-wrapper {
    max-width: 1144px;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    font-size: 1.2rem;
    margin: 0 auto;
    padding: 0;
    height: 70px;
  }
  .navbar {
    img {
      justify-self: start;
      cursor: pointer;
      width: 95px;
      height: 24px;
    }
  }
  .fa-bars {
    color: whitesmoke;
    font-size: 2rem;
  }
  .fa-times {
    color: whitesmoke;
  }
  ul {
    display: grid;
    grid-template-columns: repeat(5, auto);
    // grid-gap: 2rem;
    align-items: center;
    list-style: none;
    text-align: center;
    width: 70%;
    justify-content: end;
    li {
      display: flex;
      justify-content: center;
      a {
        text-decoration: none;
        color: rgba(245, 245, 245, 0.8);
        padding: 0 2rem;
        margin-right: 8px;
        &:hover {
          background-color: lighten($primary, 10);
          border-radius: $radius-button;
          transition: all 0.2 ease-out;
          padding: 8px 2rem;
          font-weight: 500;
          color: lighten($primary, 60);
        }
      }
    }
  }
}
// .nav-links-mobile{
//     display: none;
// }
.menu-icon {
  display: none;
}
.selected {
  background-color: lighten($primary, 10);
  border-radius: $radius-button;
  transition: all 0.2 ease-out;
  padding: 8px 2rem;
  font-weight: 500;
  color: lighten($primary, 60);
}
@media only screen and (max-width: 1144px){
  nav .navbar-wrapper {
    padding: 0 2rem;
  }
}
@media only screen and (max-width: 768px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    padding: 2rem;
    position: absolute;
    top: 70px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  .active {
    background-color: darken($primary, 10);
    left: 0;
    opacity: 3;
    transition: all 0.5s ease;
    z-index: 10;
  }
  //   .nav-links {
