  }

  nav {
    ul {
      .btn {
        margin-top: 2rem;
        width: 100% !important;
      }
      li {
        width: 100%;
        a {
          width: 100%;

          padding: 1rem 0;

          &:hover {
            background-color: lighten($primary, 10);
