// navbar icons
// burger
.burger {
  cursor: pointer;
  top: 8px;
  height: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  span {
    display: block;
    width: 2rem;
    height: 4px;
    border-radius: 4px;
    background-color: white;
  }
}

// close
.box {
  width: 2rem;
  height: 2rem;
  position: relative;
  /* Add flexbox property*/
  display: flex;
  align-items: center;
  justify-content: center;
}

.cross {
  position: absolute;
  height: 30px;
  width: 4px;
  background-color: white;
  border-radius: 4px;
}

.left {
  transform: rotate(45deg);
}

.right {
  transform: rotate(-45deg);
}

.pointer {
  cursor: pointer;
}
.animation:hover {
  transform: rotate(360deg);
  transition: ease-in-out 1s;
}
