@import "../../global";

@mixin btn-color($color) {
  color: $color;
}
@mixin btn-bgcolor($bg, $border) {
  background-color: $bg;
  border: 2px solid $border;
}
.btn {
  padding: 12px 20px;
  border-radius: $radius-button;
  font-size: 18px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  box-shadow: 0 6px rgba(255, 255, 255, 0.3);
  &:hover {
    transition: all 0.3s ease-out;
    transition: 250ms;
  }
  &:active {
    box-shadow: 0 5px rgba(255, 255, 255, 0);
    transform: translateY(6px);
  }
}


// button-color----------------------------------------------
// button-color-primary         
.btn-primary {
  @include btn-color($primary);
  &:hover {
    color: darken($primary, 30);
  }
}
// button-color-secondary
.btn-secondary {
  @include btn-color($secondary);
  &:hover {
    color: darken($primary, 30);
  }
}
// button-color----------------------------------------------


// button-background-color===================================
// button-background-color-primary         
.btn-bgprimary {
  @include btn-bgcolor($primary, $primary);
  box-shadow: 0 6px #34864644;
  &:hover {
    background-color: lighten($primary, 10);
    border: 2px solid lighten($primary, 10);
  }
}
// button-background-color-secondary
.btn-bgsecondary {
  @include btn-bgcolor($secondary, $secondary);
  &:hover {
    background-color: darken($secondary, 10);
    border: 2px solid darken($secondary, 10);
  }
}
// button-background-color===================================


// button-outline~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
// button-no-outline
.btn-full {
  
}
// button-with-outline
.btn-outline {
  background-color: rgba(255, 255, 255, 0);
}
