* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
}
h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 42px;
  color: whitesmoke;
}
h2{
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
}
.container {
  margin: 0 auto;
  max-width: 1144px;
  overflow: hidden;
}
@media only screen and (max-width: 1144px) {
  .container {
    padding: 0 2rem;
    width: auto;
  }
}
@media only screen and (max-width: 500px) {
  .container {
    padding: 0 16px;
  }
}
