@import "../../global";

.modal-wrapper {
  background-color: rgba(0, 0, 0, 0.623);
  z-index: 10;
  position: fixed;
  height: 100vh;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    height: 100vh;
    .modal {
      padding: 2rem;
      background-color: white;
      border-radius: $radius-button;
      border: 2px solid $primary;
      max-width: 400px;
      height: auto;
      display: flex;
      flex-direction: column;
      text-align: center;
      .fa-comment-alt {
        color: $primary;
        font-size: 3rem;
      }
      h5 {
        font-weight: 600;
        font-size: 20px;
        line-height: 25px;
        margin-top: 1rem;
      }
      .btn-modal {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin-top: 2rem;
        grid-gap: 1rem;
        .btn{
          width: 100%;
        }
      }
      .form-wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 1rem;
        .label {
          line-height: 2rem;
          font-weight: 500;
        }
        input {
          padding: 12px 20px;
          border-radius: $radius-button;
          width: 100%;
          outline-color: lighten($primary, 20);
          border: 2px solid rgba(0, 0, 0, 0.5);
        }
        textarea {
          padding: 12px 20px;
          height: 100px;
          border-radius: $radius-button;
          width: 100%;
          border: 2px solid rgba(0, 0, 0, 0.5);
          outline-color: lighten($primary, 20);
        }
      }
    }
  }
}
.is-danger{
  font-size: 14px;
  color: red;
  padding-top: 6px;
}